import BuzzGuruIcon from '@buzzguru/ui/assets/icons/buzzguru-logo-custom';
import TypographyBase from '@buzzguru/ui/Typography2';
import styled from '@emotion/styled';
import ButtonBase from '@lskjs/button';
import FormSubmitBase from '@lskjs/form/FormSubmit';
import Modal from '@lskjs/modal';
import getTheme from '@lskjs/theme/getTheme';
import Dropdown from '@lskjs/ui/Dropdown';
import { injectGlobal } from 'emotion';
import { Form } from 'formik';

export const BuzzGuru = styled(BuzzGuruIcon)`
  width: 250px;
  margin: 70px auto;
  color: ${(p) => getTheme(p.theme, 'colors.logo')};
`;

export const StyledForm = styled(Form)`
  && > div > div:first-of-type > div > div {
    flex-direction: row;
  }
  && > div:last-of-type {
    display: none;
  }
`;

export const Typography = styled(TypographyBase)`
  color: ${(p) => getTheme(p.theme, 'colors.text')};
`;

export const TypographyGray = styled(TypographyBase)`
  color: #9b9b9b;
`;

export const Button = styled(ButtonBase)`
  background: ${(p) => getTheme(p.theme, 'colors.main')};
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.mainHover')} !important;
  }
  &:active,
  &:focus {
    background-color: ${(p) => getTheme(p.theme, 'colors.main')};
  }
`;
export const FormSubmit = styled(FormSubmitBase)`
  && * {
    flex-direction: row;
  }
  background: ${(p) => getTheme(p.theme, 'colors.main')} !important;
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.mainHover')} !important;
  }
  &:active,
  &:focus {
    background: ${(p) => getTheme(p.theme, 'colors.main')} !important;
  }
`;

export const DropdownItem = styled(Dropdown.Item)`
  cursor: pointer;
  text-decoration: none;
  padding: 12px 30px 12px 12px;
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.header')};
  }
  > div:last-of-type {
    color: ${(p) => getTheme(p.theme, 'colors.white')};
  }
  &:not(:last-child) {
    border-bottom: 1px solid ${(p) => getTheme(p.theme, 'colors.border')};
  }
`;

export const ModalContent = styled(Modal.Content)`
  background-color: ${(p) => getTheme(p.theme, 'colors.loginModal')};
  border-radius: 8px;
  > * input {
    background-color: ${(p) => getTheme(p.theme, 'colors.input')};
    color: ${(p) => getTheme(p.theme, 'colors.inputText')};
    border: 1px solid ${(p) => getTheme(p.theme, 'colors.inputBorder')};
  }
`;

export const CenterWrapper = styled('div')`
  margin-top: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
`;

export const Error = styled('div')`
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Gotham Pro';
  font-size: 11px;
  font-weight: 500;
  line-height: 40px;
  letter-spacing: -0.01em;
  margin-bottom: -20px;
  border-radius: 4px;
  color: ${(p) => getTheme(p.theme, 'colors.errorText')} !important;
  background-color: ${(p) => getTheme(p.theme, 'colors.errorBackground')};
`;

export const globalStyles = () => injectGlobal`
  .ReactModal__Overlay {
    z-index: 99999 !important;
  }
`;

export default DropdownItem;
