import PropTypes from 'prop-types';
import React from 'react';

import LeftContent from './LeftContent';
import RightContent from './RightContent';
import { Wrapper } from './TopContent.styles';

const TopContent = ({ entity, app, view, theme, login, extensionFavorite }) => (
  <Wrapper>
    <LeftContent entity={entity} view={view} theme={theme} />
    <RightContent entity={entity} app={app} login={login} extensionFavorite={extensionFavorite} />
  </Wrapper>
);

TopContent.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  app: PropTypes.objectOf(Object).isRequired,
  login: PropTypes.func.isRequired,
  extensionFavorite: PropTypes.func.isRequired,
  view: PropTypes.oneOf(['short', 'long']),
};
TopContent.defaultProps = {
  view: 'short',
};

export default TopContent;
