import CrudApi from '@lskjs/mobx/mobxStores/CrudApi';

import xhrAdapter from './axiosUserscriptAdapter';

export class CSPCrudApi extends CrudApi {
  async run() {
    await super.run();
    this.client.interceptors.request.use((request) => {
      const token = window?.__lskjs?.GM_getValue('token');
      if (token) {
        request.headers.Authorization = `Bearer ${token}`;
      }
      return request;
    });
    this.client.defaults.adapter = xhrAdapter;
  }
}

export default CSPCrudApi;
