import ExtErrorIcon from '@buzzguru/ui/assets/icons/ext-error';
import ExtWarningIcon from '@buzzguru/ui/assets/icons/ext-warning';
import T from '@lskjs/t';
import React from 'react';

import Wrapper, { IconWrapper, Typography } from './Alert.styles';

const Alert = ({ subscribers = 0, ...props }) => {
  // console.log({ alertSubscribers: subscribers });
  let title;
  let icon;
  title = <T name="extention.alert.noData" />;
  icon = <ExtErrorIcon size={24} />;
  if (typeof subscribers === 'number' && subscribers >= 10000) {
    title = <T name="extention.alert.warning" />;
    icon = <ExtWarningIcon size={24} />;
  }
  if (typeof subscribers === 'number' && subscribers < 10000) {
    title = <T name="extention.alert.error" />;
    icon = <ExtWarningIcon size={24} />;
  }
  if (typeof subscribers === 'string' && subscribers === 'noData') {
    title = <T name="extention.alert.noData" />;
    icon = <ExtErrorIcon size={24} />;
  }
  return (
    <Wrapper {...props}>
      <IconWrapper>{icon}</IconWrapper>
      <Typography view="extra-1" lineHeight="24px" letterSpacing="-0.01em" style={{ whiteSpace: 'pre-line' }}>
        {title}
      </Typography>
    </Wrapper>
  );
};

export default Alert;
