// @match        *://hypeauditor.com/*
// @match        *://*.hypeauditor.com/*

import { addInterceptor } from '../utils';

export async function hypeauditor() {
  return addInterceptor({
    urls: ['://app.hypeauditor.com'],
    xhrUrls: /^\/discovery\/ajax\/.*$/,

    provider: 'hypeauditor',
    endpoint: 'discovery/ajax',
  });
}

export default hypeauditor;
