const Mention = ({ entity, i18, view }) => {
  const ctx = useContext(ToolbarContext);
  const { theme } = ctx;
  const extTheme = getExtTheme(theme);
  const { item: baseItem } = entity;
  const [type, setType] = useState('youtube');
  let item = baseItem;
  if (type === 'youtubeVideo' || type === 'twitchStream') {
    item = baseItem.channel;
  }
  const getUrlType = () => {
    switch (type) {
      case 'youtubeVideo':
      case 'youtube':
        return 'youtube';
      case 'twitchStream':
      case 'twitch':
        return 'twitch';
      case 'instagram':
        return 'instagram';
      case 'tiktok':
        return 'tiktok';
      default:
        return type;
    }
  };
  const appMentions = item?.mentionsApps || {};
  appMentions.items = [
    ...(appMentions?.items || []).filter(
      ({ product }) => (product?.info?.image || '').length > 0 && !(product?.info?.image || '').includes('default.png'),
    ),
    ...(appMentions?.items || []).filter(
      ({ product }) =>
        (product?.info?.image || '').length === 0 || (product?.info?.image || '').includes('default.png'),
    ),
  ];
  const gameMentions = item?.mentionsGames || {};
  gameMentions.items = [
    ...(gameMentions?.items || []).filter(
      ({ product }) => (product?.info?.image || '').length > 0 && !(product?.info?.image || '').includes('default.png'),
    ),
    ...(gameMentions?.items || []).filter(
      ({ product }) =>
        (product?.info?.image || '').length === 0 || (product?.info?.image || '').includes('default.png'),
    ),
  ];
  let mentionsChannels = [];
  if (type === 'instagram') {
    mentionsChannels = (item?.mentionsChannels || []).sort((a, b) => a._id - b._id);
  }
  let tiktokMentionsChannels = [];
  if (type === 'tiktok') {
    tiktokMentionsChannels = (item?.mentionsChannels?.data || [])
      .map((mention) => mention?.mentionedChannel)
      .sort((a, b) => a._id - b._id);
  }
  const siteMentions = item?.mentionsSites || {};

  const appsTotal = appMentions?.totals?.total || appMentions?.totals?.totalUniqProductsCount || 0;
  const gamesTotal = gameMentions?.totals?.total || gameMentions?.totals?.totalUniqProductsCount || 0;
  const sitesTotal = siteMentions?.totals?.total || siteMentions?.totals?.totalUniqProductsCount || 0;
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, [document?.location?.href || '']);
  return (
    <Wrapper view={view}>
      {(appMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${getUrlType()}/channels/${item?._id}/products`}>
          <Tooltip
            id="apps-mentions"
            place="top"
            overlay={i18.t('productMentions.mentionOfProduct')}
            style={{ marginRight: appsTotal > 3 ? 0 : 12, textDecoration: 'none' }}
          >
            {(appMentions?.items || []).slice(0, 3).map(({ product }) => {
              if (!product?.info?.image || (product.info?.image || '').includes('default.png')) {
                return (
                  <MoreWrapper
                    style={{
                      border: `2px solid ${extTheme?.colors?.background}`,
                      borderRadius: 8,
                      marginRight: -12,
                      zIndex: 0,
                    }}
                  >
                    <App style={{ color: '#313137', width: 20, height: 'auto' }} />
                  </MoreWrapper>
                );
              }
              return (
                <Avatar
                  id={product?.id}
                  key={product?.id}
                  size={36}
                  src={product?.info?.image}
                  defaultAvatar="https://buzzguru.com/assets/images/product.png"
                  shape="square"
                  innerStyle={{
                    borderRadius: 8,
                    border: `2px solid ${extTheme?.colors?.border || '#313137'}`,
                  }}
                  style={{
                    marginRight: -12,
                    background: 'none',
                  }}
                />
              );
            })}
            {appsTotal - 3 > 0 ? <MoreWrapper>{`+${appsTotal - 3}`}</MoreWrapper> : ''}
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {(gameMentions.items || []).length ? (
        <Link
          target="_blank"
          href={`https://buzzguru.com/${getUrlType()}/channels/${item?._id}/games`}
          style={{ textDecoration: 'none' }}
        >
          <Tooltip
            id="games-mentions"
            place="top"
            overlay={i18.t('gamesMentions.mentionOfGame')}
            style={{ marginRight: gamesTotal > 3 ? 0 : 12 }}
          >
            {(gameMentions?.items || []).slice(0, 3).map(({ product }) => {
              if (!product?.info?.image || (product.info?.image || '').includes('default.png')) {
                return (
                  <MoreWrapper
                    style={{
                      border: `2px solid ${extTheme?.colors?.background}`,
                      borderRadius: 8,
                      marginRight: -12,
                      zIndex: 0,
                    }}
                  >
                    <Game style={{ color: '#313137', width: 20, height: 'auto' }} />
                  </MoreWrapper>
                );
              }
              return (
                <Avatar
                  id={product?.id}
                  key={product?.id}
                  size={36}
                  src={product?.info?.image}
                  defaultAvatar="https://buzzguru.com/assets/images/product.png"
                  shape="square"
                  innerStyle={{
                    borderRadius: 8,
                    border: `2px solid ${extTheme?.colors?.border || '#313137'}`,
                  }}
                  style={{
                    marginRight: -12,
                    background: 'none',
                  }}
                />
              );
            })}
            {gamesTotal - 3 > 0 ? <MoreWrapper>{`+${gamesTotal - 3}`}</MoreWrapper> : ''}
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {(mentionsChannels || []).length ? (
        <>
          {(mentionsChannels || []).slice(0, 4).map((channel) => (
            <Tooltip key={channel?._id} id="channel-mentions" place="top" overlay={channel?.name}>
              <TiktokLink target="_blank" href={`https://buzzguru.com/${getUrlType()}/channels/${channel?._id}`}>
                <Avatar
                  title={channel?.name}
                  src={channel?.avatar}
                  defaultAvatar={theme === 'light' ? UserLight : UserDark}
                  size={36}
                  cspStyle={{
                    marginRight: -12,
                    borderRadius: '50px',
                    border: `2px solid ${extTheme?.colors?.background || '#313137'}`,
                  }}
                  // style={{ marginRight: 12 }}
                />
              </TiktokLink>
            </Tooltip>
          ))}
          {(mentionsChannels || []).length - 4 > 0 ? (
            <Tooltip
              id="channel-mentions-total"
              place="top"
              overlay={i18.t('videoSitesBlock.mentionAll')}
              style={{ zIndex: 99999 }}
            >
              <Link
                target="_blank"
                href={`https://buzzguru.com/${type}/channels/${item?._id}`}
                style={{ textDecoration: 'none' }}
              >
                <MoreWrapper style={{ borderRadius: '50%' }}>{`+${(mentionsChannels || []).length - 4}`}</MoreWrapper>
              </Link>
            </Tooltip>
          ) : (
            ''
          )}
        </>
      ) : (
        ''
      )}
      {(tiktokMentionsChannels || []).length ? (
        <>
          {(tiktokMentionsChannels || []).slice(0, 4).map((channel) => (
            <Tooltip key={channel?._id} id="tiktok-channel-mentions" place="top" overlay={channel?.name}>
              <TiktokLink target="_blank" href={`https://buzzguru.com/${getUrlType()}/channels/${channel?._id}`}>
                <Avatar
                  title={channel?.name}
                  src={channel?.tiktokImage}
                  defaultAvatar={theme === 'light' ? UserLight : UserDark}
                  size={36}
                  // style={{ marginRight: 12 }}
                />
              </TiktokLink>
            </Tooltip>
          ))}
          {(item?.mentionsChannels?.count || 0) - 4 > 0 ? (
            <Tooltip
              id="channel-mentions-total"
              place="top"
              overlay={i18.t('videoSitesBlock.mentionAll')}
              style={{ zIndex: 99999 }}
            >
              <Link
                target="_blank"
                href={`https://buzzguru.com/${type}/channels/${item?._id}`}
                style={{ textDecoration: 'none' }}
              >
                <MoreWrapper style={{ borderRadius: '50%' }}>{`+${
                  (item?.mentionsChannels?.count || 0) - 4
                }`}</MoreWrapper>
              </Link>
            </Tooltip>
          ) : (
            ''
          )}
        </>
      ) : (
        ''
      )}
      {(siteMentions.items || []).length ? (
        <Link
          target="_blank"
          href={`https://buzzguru.com/${getUrlType()}/channels/${item?._id}/sites`}
          style={{ textDecoration: 'none' }}
        >
          <Tooltip id="site-mentions" place="top" overlay={i18.t('productMentions.mentionOfSite')}>
            {sitesTotal > 0 ? (
              <MoreWrapper style={{ width: 'auto', overflow: 'hidden' }}>
                <img alt="Sites" src={SiteLogos} />
                <Typography style={{ padding: '0 8px' }} view="title-2">
                  {i18.t('extention.moreSites', { count: sitesTotal })}
                </Typography>
              </MoreWrapper>
            ) : (
              ''
            )}
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
    </Wrapper>
  );
};

Mention.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Mention);
