/* global window document */
// import Quality from '@buzzguru/ui/kit/Quality';
// import Button from '@lskjs/button';
import UserDark from '@buzzguru/ui/assets/icons/png/user-dark-square.png';
import UserLight from '@buzzguru/ui/assets/icons/png/user-light.png';
import Tooltip from '@buzzguru/ui/Tooltip';
import Link from '@lskjs/link';
import truncate from 'lodash/truncate';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import { build, isDebug } from '../../../../../utils/env';
import getRequestParams from '../../../utils/getRequestParams';
import Avatar from '../../Avatar';
import Alert from '../../BottomContent/Alert';
import Mention from '../../BottomContent/Mention';
import Stats from '../../BottomContent/Stats';
import FlagIcon from '../../FlagIcon';
import { BuzzGuru, FlagWrapper, globalStyles, LanguageWrapper, Quality, Title, Wrapper } from './LeftContent.styles';
import Relations from './Relations';

globalStyles();

const LeftContent = ({ entity, view, theme, i18 }) => {
  let { item } = entity;
  const [parsedItem, setParsedItem] = useState({});
  const general = item?.generalScore || item?.channel?.generalScore;
  const isAuth = window?.__lskjs?.GM_getValue('session') || null;
  if (!item?._id) {
    item = parsedItem;
  }

  useEffect(() => {
    getRequestParams().then((res) => setParsedItem(res));
  }, [document?.location?.href || '']);
  return (
    <Wrapper>
      <Link href={'https://buzzguru.com/home'} target="_blank">
        <BuzzGuru className={build} />
      </Link>
      {isDebug ? <span style={{ position: 'absolute', left: 0, bottom: 2 }}>{build}</span> : null}
      {view === 'short' ? (
        <>
          <Avatar
            title={item?.name || item?.channel?.name}
            src={item?.avatar || item?.channel?.avatar}
            defaultAvatar={theme === 'light' ? UserLight : UserDark}
            size={36}
            cspStyle={{
              marginRight: 12,
              borderRadius: '50px',
            }}
            style={{ marginRight: 12 }}
          />
          <Title>{truncate(item?.name || item?.channel?.name, { length: 20 })}</Title>
        </>
      ) : (
        ''
      )}
      {/* <Button
        style={{ width: 24, height: 24, borderRadius: 4, backgroundColor: '#4A4A4A80', marginRight: 12 }}
        view="empty"
        isRipple={false}
        icon={<FlagIcon code={item?.channelInfo?.country || ''} style={{ width: 16, height: 12 }} />}
      /> */}
      {item?.country || item?.channel?.country ? (
        <FlagWrapper extView={view}>
          <FlagIcon
            code={item?.country || item?.channel?.country || ''}
            style={{ width: 16, height: 12, maxWidth: 'none' }}
            placement="top"
          />
        </FlagWrapper>
      ) : (
        ''
      )}
      {item?.language || item?.channel?.language ? (
        <Tooltip
          id={`ex-lang-${item?.language || item?.channel?.language}`}
          place="top"
          overlay={i18.t(`locales.${item?.language || item?.channel?.language}`)}
        >
          <LanguageWrapper extView={view}>
            {String(item?.language || item?.channel?.language)
              .slice(0, 2)
              .toUpperCase()}
          </LanguageWrapper>
        </Tooltip>
      ) : (
        ''
      )}
      {view === 'short' || !item?._id ? '' : <Quality extView={view} percent={general} />}
      {view === 'long' && !item?._id ? (
        <Alert subscribers={parsedItem?.subscribers || 'noData'} style={{ margin: 'auto' }} />
      ) : (
        ''
      )}
      {view === 'short' ? (
        <>
          <Relations entity={entity} />
          {item?._id ? <Quality percent={general} /> : ''}
        </>
      ) : (
        <>{item?._id ? <Stats entity={entity} style={{ marginLeft: 16 }} view={view} /> : ''}</>
      )}
      {isAuth && view === 'long' ? <Mention entity={entity} view={view} style={{ marginLeft: 16 }} /> : ''}
    </Wrapper>
  );
};

LeftContent.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
  view: PropTypes.oneOf(['short', 'long']),
  theme: PropTypes.oneOf(['dark', 'light', 'auto']),
};
LeftContent.defaultProps = {
  view: 'short',
  theme: 'dark',
};

export default inject('i18')(LeftContent);
