/* eslint-disable no-param-reassign */
/* global document window */

// @match        *://instagram.com/*
// @match        *://*.instagram.com/*

import { addXMLRequestCallback } from '../utils';

const isDev = 0;
const host = isDev ? 'http://127.0.0.1:8000' : 'https://worker.buzz.guru';
const interceptEndpoint = `${host}/api/instagram/intercept`;
const reqBody = {
  method: 'POST',
  url: interceptEndpoint,
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
  },
};

function sharedDataGetter() {
  if (
    window._sharedData &&
    window._sharedData.entry_data &&
    window._sharedData.entry_data.ProfilePage &&
    Array.isArray(window._sharedData.entry_data.ProfilePage) &&
    window._sharedData.entry_data.ProfilePage.length
  ) {
    const profilePage = window._sharedData.entry_data.ProfilePage[0];
    if (profilePage.graphql && profilePage.graphql.user) {
      const { user } = profilePage.graphql;
      const payload = {
        url: `/${user.username}/?__a=1`,
        request: {
          method: 'GET',
        },
        response: {
          text: JSON.stringify(profilePage),
        },
      };
      window.__lskjs.GM_xmlhttpRequest({
        ...reqBody,
        data: JSON.stringify(payload),
      });
    }
  }
}

export async function instagram() {
  const location = document?.location?.href || '';
  if (!location.includes('://www.instagram.com')) return { status: 'ignoreDomain' };

  sharedDataGetter();

  addXMLRequestCallback(async (xhr) => {
    const responseHeaders = {};
    const responseHeadersArray = xhr
      .getAllResponseHeaders()
      .split('\n')
      .filter((str) => str);
    responseHeadersArray.forEach((header) => {
      const [key, value] = header.split(/: (.+)/);
      responseHeaders[key] = value;
    });
    const payload = {
      url: xhr.meta.url,
      request: {
        method: xhr.meta.method,
        headers: xhr.requestHeaders,
        body: xhr.meta.body,
      },
      response: {
        headers: responseHeaders,
        text: xhr.responseText,
      },
    };

    console.log('[R]', payload); // eslint-disable-line no-console
    window.__lskjs.GM_xmlhttpRequest({
      ...reqBody,
      data: JSON.stringify(payload),
    });
  });
  return { status: 'success' };
}

export default instagram;
