import styled from '@emotion/styled';
import Button from '@lskjs/button';
import getTheme from '@lskjs/theme/getTheme';
import Dropdown from '@lskjs/ui/Dropdown';

export const DropdownItem = styled(Dropdown.Item)`
  cursor: pointer;
  text-decoration: none;
  padding: 12px 30px 12px 12px;
  background-color: ${(p) => getTheme(p.theme, 'colors.background')};
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.buttonHover')};
  }
  > div:last-of-type {
    color: ${(p) => getTheme(p.theme, 'colors.text')};
  }
  &:not(:last-child) {
    border-bottom: 1px solid ${(p) => getTheme(p.theme, 'colors.border')};
  }
`;

export const SmallHoverButton = styled(Button)`
  background-color: ${(p) => getTheme(p.theme, 'colors.button')};
  color: ${(p) => getTheme(p.theme, 'colors.text')} !important;
  width: 24px;
  height: 24px;
  border-radius: 4px;
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.buttonHover')};
  }
`;

export default DropdownItem;
