import CommentsIcon from '@buzzguru/ui/assets/icons/comment-outline';
import DateOutline from '@buzzguru/ui/assets/icons/date-outline';
import ExtPriceIcon from '@buzzguru/ui/assets/icons/ext-cost';
import ExtErIcon from '@buzzguru/ui/assets/icons/ext-engagement';
import ExtCpmIcon from '@buzzguru/ui/assets/icons/ext-lightning';
import ExtLikesIcon from '@buzzguru/ui/assets/icons/ext-likes';
import ExtViewsIcon from '@buzzguru/ui/assets/icons/ext-views';
import Interaction from '@buzzguru/ui/assets/icons/interaction';
import Like from '@buzzguru/ui/assets/icons/like';
import Online from '@buzzguru/ui/assets/icons/online';
import Stream from '@buzzguru/ui/assets/icons/stream';
import ThumbDown from '@buzzguru/ui/assets/icons/thumb-down';
import ThumbUp from '@buzzguru/ui/assets/icons/thumb-up';
import Videos from '@buzzguru/ui/assets/icons/videos';
import { toShort } from '@lskjs/utils/formatter';
import get from 'lodash/get';
import React from 'react';

import beauty from './beauty';

const getStats = ({ item, i18, type }) => {
  let erMedianValue = get(item, 'er') ? get(item, 'er').toFixed(2) : 0;
  if (erMedianValue > 100) erMedianValue = 100;
  const erMedian = `${erMedianValue}%`;

  const cpmMin = get(item, 'cpmMin', 0) / 100;
  const cpmMax = get(item, 'cpmMax', 0) / 100;
  if (type === 'youtubeVideo') {
    const stats = [];
    if (get(item, 'likes', 0)) {
      stats.push({
        id: 'avgLikes',
        value: toShort(Math.ceil(get(item, 'likes', 0))),
        title: i18.t('videoPageBlock.likes'),
        icon: <ThumbUp color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'dislikes', 0)) {
      stats.push({
        id: 'avgDislikes',
        value: toShort(Math.ceil(get(item, 'dislikes', 0))),
        title: i18.t('videoPageBlock.dislikes'),
        icon: <ThumbDown color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'comments', 0)) {
      stats.push({
        id: 'comments',
        value: toShort(Math.ceil(get(item, 'comments', 0))),
        title: i18.t('exportModal.comments'),
        icon: <CommentsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'er', 0)) {
      stats.push({
        id: 'er',
        value: erMedian,
        title: (i18.t('er') || '').toUpperCase(),
        icon: <ExtErIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'publishedAt', 0)) {
      stats.push({
        id: 'publishedAt',
        value: `${new Date(get(item, 'publishedAt', '')).toLocaleDateString('ru-RU')}`,
        title: i18.t('ChannelProductVideoHeaderItem.publishedAt'),
        icon: <DateOutline color="#7070ff" size={24} />,
      });
    }
    return stats;
  }
  if (type === 'twitchStream') {
    const stats = [];
    if (get(item, 'avgViews', 0)) {
      stats.push({
        id: 'avgViews',
        value: toShort(Math.ceil(get(item, 'avgViews', 0))),
        title: i18.t('channelHeaderInfo.averageViewsCount'),
        icon: <ExtViewsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'views', 0)) {
      stats.push({
        id: 'views',
        value: toShort(Math.ceil(get(item, 'views', 0))),
        title: i18.t('streamPage.viewsVideo'),
        icon: <Videos color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'viewers', 0)) {
      stats.push({
        id: 'viewers',
        value: toShort(Math.ceil(get(item, 'viewers', 0))),
        title: i18.t('streamPage.onlineAvg'),
        icon: <Online color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'comments', 0)) {
      stats.push({
        id: 'comments',
        value: toShort(Math.ceil(get(item, 'comments', 0))),
        title: i18.t('exportModal.comments'),
        icon: <CommentsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'er', 0)) {
      stats.push({
        id: 'er',
        value: erMedian,
        title: (i18.t('er') || '').toUpperCase(),
        icon: <ExtErIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'publishedAt', 0)) {
      stats.push({
        id: 'publishedAt',
        value: `${new Date(get(item, 'publishedAt', '')).toLocaleDateString('ru-RU')}`,
        title: i18.t('ChannelProductVideoHeaderItem.publishedAt'),
        icon: <DateOutline color="#7070ff" size={24} />,
      });
    }
    return stats;
  }
  if (type === 'instagram' || type === 'tiktok') {
    const stats = [];
    if (get(item, 'likes', 0)) {
      stats.push({
        id: 'avgLikes',
        value: toShort(Math.ceil(get(item, 'likes', 0))),
        title: i18.t('channelHeaderInfo.averageLikesCount'),
        icon: <Like color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'comments', 0)) {
      stats.push({
        id: 'comments',
        value: toShort(Math.ceil(get(item, 'comments', 0))),
        title: i18.t('exportModal.comments'),
        icon: <CommentsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'views', 0)) {
      stats.push({
        id: 'avgViews',
        value: toShort(Math.ceil(get(item, 'views', 0))),
        title: i18.t('channelHeaderInfo.averageViewsCount'),
        icon: <ExtViewsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'audienceReachRangeMin', 0) && get(item, 'audienceReachRangeMax', 0)) {
      stats.push({
        id: 'audienceReachRange',
        value: `≈ ${toShort(Math.ceil(get(item, 'audienceReachRangeMin', 0)))} - ${toShort(
          Math.ceil(get(item, 'audienceReachRangeMax', 0)),
        )}`,
        title: i18.t('extension.stats.audienceReachRange'),
        icon: <ExtViewsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'er', 0)) {
      stats.push({
        id: 'er',
        value: erMedian,
        title: (i18.t('er') || '').toUpperCase(),
        icon: <ExtErIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'priceMin', 0) || get(item, 'priceMax', 0)) {
      stats.push({
        id: 'price',
        value: `$${beauty(get(item, 'priceMin', 0) / 100)} - $${beauty(get(item, 'priceMax', 0) / 100)}`,
        title: i18.t('channelHeaderInfo.intergrationCosts'),
        icon: <ExtPriceIcon color="#7070ff" size={24} />,
      });
    }
    if (cpmMin || cpmMax) {
      stats.push({
        id: 'cpm',
        value: `$${cpmMin}-${cpmMax}`,
        title: i18.t('channel.cost.cpm.footer'),
        icon: <ExtCpmIcon color="#7070ff" size={24} />,
      });
    }

    if (type === 'instagram' && get(item, 'estimatedAudienceReach', 0)) {
      stats.push({
        id: 'estimatedAudienceReach',
        value: toShort(Math.ceil(get(item, 'estimatedAudienceReach', 0))),
        title: i18.t('instagramChannel.costBlock.interaction.title'),
        icon: <Interaction color="#7070ff" size={24} />,
      });
    }
    return stats;
  }
  if (type === 'twitch') {
    const stats = [];
    if (get(item, 'viewers', 0)) {
      stats.push({
        id: 'viewers',
        value: toShort(Math.ceil(get(item, 'viewers', 0))),
        title: i18.t('streamPage.onlineAvg'),
        icon: <Online color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'views', 0)) {
      stats.push({
        id: 'views',
        value: toShort(Math.ceil(get(item, 'views', 0))),
        title: i18.t('streamPage.viewsVideo'),
        icon: <ExtViewsIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'er', 0)) {
      stats.push({
        id: 'er',
        value: erMedian,
        title: (i18.t('er') || '').toUpperCase(),
        icon: <ExtErIcon color="#7070ff" size={24} />,
      });
    }
    if (get(item, 'priceMin', 0) || get(item, 'priceMax', 0)) {
      stats.push({
        id: 'price',
        value: `$${beauty(get(item, 'priceMin', 0) / 100)} - $${beauty(get(item, 'priceMax', 0) / 100)}`,
        title: i18.t('channelHeaderInfo.intergrationCosts'),
        icon: <ExtPriceIcon color="#7070ff" size={24} />,
      });
    }
    if (cpmMin || cpmMax) {
      stats.push({
        id: 'cpm',
        value: `$${cpmMin}-${cpmMax}`,
        title: i18.t('channel.cost.cpm.footer'),
        icon: <ExtCpmIcon color="#7070ff" size={24} />,
      });
    }
    return stats;
  }
  // Default youtube
  return [
    {
      id: 'avgViews',
      value: toShort(Math.ceil(get(item, 'views', 0))),
      title: i18.t('channelHeaderInfo.averageViewsCount'),
      icon: <ExtViewsIcon color="#7070ff" size={24} />,
    },
    {
      id: 'avgLikes',
      value: toShort(Math.ceil(get(item, 'likes', 0))),
      title: i18.t('channelHeaderInfo.averageLikesCount'),
      icon: <ExtLikesIcon color="#7070ff" size={24} />,
    },
    {
      id: 'price',
      value: `$${beauty(get(item, 'priceMin', 0) / 100)} - $${beauty(get(item, 'priceMax', 0) / 100)}`,
      title: i18.t('channelHeaderInfo.intergrationCosts'),
      icon: <ExtPriceIcon color="#7070ff" size={24} />,
    },
    {
      id: 'er',
      value: erMedian,
      title: (i18.t('er') || '').toUpperCase(),
      icon: <ExtErIcon color="#7070ff" size={24} />,
    },
    {
      id: 'cpm',
      value: `$${cpmMin}-${cpmMax}`,
      title: i18.t('channel.cost.cpm.footer'),
      icon: <ExtCpmIcon color="#7070ff" size={24} />,
    },
  ];
};

export default getStats;
