/* global document */
import get from 'lodash/get';

export default (item, i18, type = 'youtube') => {
  const socials = [
    'facebook',
    'vk',
    'twitch',
    'twitter',
    'instagram',
    'telegram',
    // 'email',
    'discord',
    'youtube',
    'phone',
    'tiktok',
  ];
  const generateUrl = (social, socialLink) => {
    switch (social) {
      case 'facebook':
        return `https://www.facebook.com/${socialLink}`;
      case 'vk':
        return `https://vk.com/${socialLink}`;
      case 'twitch':
        return `https://www.twitch.tv/${socialLink}`;
      case 'twitter':
        return `https://twitter.com/${socialLink}`;
      case 'instagram':
        return `https://instagram.com/${socialLink}`;
      case 'telegram':
        return `https://t.me/${socialLink}`;
      case 'email':
        return `mailto:${socialLink}`;
      case 'discord':
        return `https://discord.gg/${socialLink}`;
      case 'phone':
        return `tel:${socialLink}`;
      case 'tiktok':
        return `https://www.tiktok.com/@${socialLink}`;
      case 'youtube':
        return socialLink;
      default:
        return socialLink;
    }
  };
  if (!item) return [];
  if (type === 'twitch') {
    return [
      {
        USERNAME: get(item, 'name', ''),
      },
      {
        LINK: document?.location?.href || '',
      },
      {
        TRACKER: (document?.location?.href || '').replace('twitch.tv', 'twitchtracker.com'),
      },
      {
        COUNTRY: get(item, 'country', '') ? i18.t(`country.${get(item, 'country', '')}`) : '',
      },
      {
        LANGUAGE: get(item, 'language', '') ? i18.t(`locales.${get(item, 'language', '')}`) : '',
      },
      {
        FOLLOWERS: get(item, 'subscribers', ''),
      },
      {
        CCV: Math.round(get(item, 'viewers', 0)) || '',
      },
      {
        CONTENT: get(item, 'content', ''),
      },
      {
        EMAIL: Array.isArray(get(item, 'pageContacts.email', ''))
          ? get(item, 'pageContacts.email').join(', ')
          : get(item, 'pageContacts.email', ''),
      },
      {
        OTHER_CONTACT: socials
          .map((social) => {
            const contacts = get(item, `pageContacts.${social}`, []).map((contact) => generateUrl(social, contact));
            return contacts.length ? contacts.join(', ') : '';
          })
          .filter((it) => !!it)
          .join(', '),
      },
    ];
  }
  if (type === 'tiktok') {
    return [
      {
        USERNAME: get(item, 'name', ''),
      },
      {
        LINK: document?.location?.href || '',
      },
      {
        COUNTRY: get(item, 'country', '') ? i18.t(`country.${get(item, 'country', '')}`) : '',
      },
      {
        FOLLOWERS: get(item, 'subscribers', ''),
      },
      {
        VIEWS: get(item, 'views', ''),
      },
      {
        CONTENT: get(item, 'content', ''),
      },
      {
        EMAIL: get(item, 'contacts', [])
          .filter(({ contactType }) => contactType === 'email')
          .map(({ url }) => url)
          .join(', '),
      },
      {
        OTHER_CONTACT: get(item, 'contacts', [])
          .filter(({ contactType }) => contactType !== 'email')
          .map(({ id, contactType }) => generateUrl(contactType, id))
          .filter((it) => !!it)
          .join(', '),
      },
      {
        OTHER_LINKS: Array.isArray(get(item, 'bioLink', ''))
          ? get(item, 'bioLink', '').join(', ')
          : get(item, 'bioLink', ''),
      },
      {
        ER: get(item, 'er', ''),
      },
      {
        REACH: get(item, 'likes', 0) * 2,
      },
    ];
  }
  if (type === 'instagram') {
    return [
      {
        USERNAME: get(item, 'name', ''),
      },
      {
        LINK: document?.location?.href || '',
      },
      {
        COUNTRY: get(item, 'country', '') ? i18.t(`country.${get(item, 'country', '')}`) : '',
      },
      {
        FOLLOWERS: get(item, 'subscribers', ''),
      },
      {
        LIKES: get(item, 'likes', ''),
      },
      {
        VIEWS:
          get(item, 'audienceReachRangeMin', '') || get(item, 'audienceReachRangeMax', '')
            ? `${get(item, 'audienceReachRangeMin', '')} - ${get(item, 'audienceReachRangeMax', '')}`
            : '',
      },
      {
        CONTENT: get(item, 'content', ''),
      },
      {
        EMAIL: Array.isArray(get(item, 'contacts.email', ''))
          ? get(item, 'contacts.email').join(', ')
          : get(item, 'contacts.email', ''),
      },
      {
        OTHER_CONTACT: socials
          .map((social) => {
            const contacts = get(item, `contacts.${social}`, []).map((contact) => generateUrl(social, contact));
            return contacts.length ? contacts.join(', ') : '';
          })
          .filter((it) => !!it)
          .join(', '),
      },
      {
        OTHER_LINKS: get(item, 'links', []).join(', '),
      },
      {
        ER: get(item, 'er', ''),
      },
      {
        REACH: get(item, 'likes', 0) * 2,
      },
    ];
  }
  // default youtube
  return [
    {
      USERNAME: get(item, 'name', ''),
    },
    {
      LINK: document?.location?.href || '',
    },
    {
      EMAIL: get(item, 'links', [])
        .filter(({ contactType }) => contactType === 'email')
        .map(({ url }) => url)
        .join(', '),
    },
    {
      OTHER_CONTACT: get(item, 'links', [])
        .filter(({ contactType }) => contactType !== 'email')
        .map(({ contactType, url }) => generateUrl(contactType, url))
        .filter((it) => !!it)
        .join(', '),
    },
    {
      COUNTRY: get(item, 'country', '') ? i18.t(`country.${get(item, 'country', '')}`) : '',
    },
    {
      LANGUAGE: get(item, 'language', '') ? i18.t(`locales.${get(item, 'language', '')}`) : '',
    },
    {
      CONTENT: get(item, 'content', ''),
    },
    {
      SUBSCRIBERS: get(item, 'subscribers', ''),
    },
    {
      AV_VIEWS: get(item, 'views', ''),
    },
  ];
};
