/* eslint-disable no-undef */
/* global window */
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

// import addFetchSimpleInterceptor from '../../utils/addFetchSimpleInterceptor';
import Trigger from './components/Trigger';
import { Wrapper } from './Toolbar.styles';
import getRequestParams from './utils/getRequestParams';

const instagramRegexp = /(?:(?:http|https):\/\/)?(?:www.)?(?:instagram.com|instagr.am)\/([A-Za-z0-9-_.]+)/gim;

export const ToolbarContext = React.createContext({ isOpen: false, view: 'short', theme: 'dark' });

function waitForAddedNode(params) {
  const elFirst = document.querySelector(params.id);
  if (elFirst) {
    params.done(elFirst);
    return undefined;
  }
  const mutation = new MutationObserver(function () {
    const el = document.querySelector(params.id);
    if (el) {
      this.disconnect();
    }
    params.done(el);
  });
  mutation.observe(document.body, {
    subtree: true,
    childList: true,
  });
  return mutation;
}

const Toolbar = ({ children }) => {
  // console.log(`window?.__lskjs?.GM_getValue('isOpenExtention')`, window?.__lskjs?.GM_getValue('isOpenExtention'));
  const [isOpen, setOpen] = useState(window?.__lskjs?.GM_getValue('isOpenExtention') || true);
  const [view, setView] = useState(window?.__lskjs?.GM_getValue('view') || 'short');
  const [theme, setTheme] = useState(window?.__lskjs?.GM_getValue('theme') || 'dark');
  const [allowedPatch, setAllowedPatch] = useState(null);
  const canWeInjectToolbar = (params) => getRequestParams(params).then((res) => setAllowedPatch(res));
  useEffect(() => {
    // наверное перенести проверки в отдельный файл
    if (document.location.href.includes('youtube.com')) {
      document.addEventListener('yt-navigate-finish', (e) => {
        canWeInjectToolbar();
      });
      document.addEventListener('fullscreenchange', () => {
        if (document.fullscreenElement) {
          const ytdApp = (document.querySelectorAll('ytd-app') || [])[0];
          if (ytdApp) ytdApp.style.height = `${(window?.innerHeight || 0) * 1.5}px`;
          setAllowedPatch(null);
        } else {
          canWeInjectToolbar();
        }
      });
      canWeInjectToolbar();
    } else if (document.location.href.includes('twitch.tv')) {
      const waitPlayerAndCheck = () =>
        waitForAddedNode({
          id: '.channel-root__player',
          parent: document.body,
          done(el) {
            if (el) {
              canWeInjectToolbar();
            } else {
              setAllowedPatch(null);
            }
          },
        });
      let observer = waitPlayerAndCheck();
      window.addEventListener('locationchange', () => {
        observer?.disconnect();
        observer = waitPlayerAndCheck();
      });
    } else if (document.location.href.includes('instagram.com')) {
      if (!window.isExtension) {
        window.csp = true; // помечаем инсту content security
      }
      const waitTitleAndCheck = () =>
        waitForAddedNode({
          id: 'section h2',
          parent: document.body,
          done(el) {
            // const match = instagramRegexp.exec(document.location.href);
            // if (match && match[1] === el?.innerText) {
            if (el) {
              canWeInjectToolbar();
            } else {
              setAllowedPatch(null);
            }
          },
        });
      let observer = waitTitleAndCheck();
      window.addEventListener('locationchange', () => {
        observer?.disconnect();
        observer = waitTitleAndCheck();
      });
    } else if (document.location.href.includes('tiktok.com')) {
      window.addEventListener('locationchange', () => {
        canWeInjectToolbar();
      });
      canWeInjectToolbar();
    }
  }, []);
  // console.log({ allowedPatch }, allowedPatch?.type);
  const triggerOpen = () => {
    window?.__lskjs?.GM_setValue('isOpenExtention', !isOpen);
    setOpen(!isOpen);
  };
  const handleSetView = (value) => {
    window?.__lskjs?.GM_setValue('view', value);
    setView(value);
  };
  const handleSetTheme = (value) => {
    window?.__lskjs?.GM_setValue('theme', value);
    setTheme(value);
  };

  if (!allowedPatch) return null;

  return (
    <Wrapper long={isOpen && view === 'long'}>
      <ToolbarContext.Provider
        value={{
          isOpen,
          theme,
          view,
          triggerOpen,
          setView: handleSetView,
          setTheme: handleSetTheme,
          allowedPatch,
        }}
      >
        {children}
      </ToolbarContext.Provider>
      {isOpen ? '' : <Trigger onClick={triggerOpen} />}
    </Wrapper>
  );
};

Toolbar.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Toolbar;
