/* global document window */

// @match        *://igram.io/*

import { addGlobalStyle } from '../utils';

export async function igram() {
  const location = document?.location?.href || '';
  if (!location.includes('igram.io')) return { status: 'ignoreDomain' };

  let spam = '.fc-consent-root, header, footer, .py-10, form + div, form + div + div, form + div + div + div, ins';
  if (!location.includes('/dl/')) {
    spam += ', main';
  }

  addGlobalStyle(`
${spam} {
  display: none !important;
}
  `);

  return { status: 'success' };
}

export default igram;
