/* global window */
export default (theme) => {
  const defaultDark = window?.matchMedia('(prefers-color-scheme: dark)')?.matches;
  const autoDark = theme === 'auto' && defaultDark;
  const mainScheme = {
    fontFamily: "'Gotham Pro', Helvetica, Arial",
    radius: '8px',
    borderRadius: '8px',
    borderRadiusLarge: '12px',
    borderCircle: '100vw',
    borderSmall: '8px',
    gridGap: 8,
    tablePadding: 16,
    layout: {
      phone: 540,
      tablet: 720,
      smallDesktop: 960,
      largeDesktop: 1184,
    },
    colors: {
      errorText: '#9B9B9B',
      errorBackground: '#E3E3E3',
      main: '#7070ff',
      mainHover: '#8C8CFF',
    },
  };
  if (theme === 'dark' || autoDark) {
    return {
      ...mainScheme,
      colors: {
        ...mainScheme.colors,
        text: '#fff',
        border: '#3d3d40',
        button: '#4c4c56',
        buttonHover: '#666671',
        closeButton: '#9b9b9b',
        background: '#313137',
        logo: '#fff',
        loginModal: '#121212',
        input: '#4a4a4a80',
        inputText: '#9b9b9b',
        inputBorder: '#4a4a4a',
        radio: '#4c4c56',
        radioActive: '#666671',
        radioTextActive: 'rgba(255, 255, 255, 1)',
        radioText: 'rgba(255, 255, 255, 0.3)',
      },
    };
  }
  return {
    ...mainScheme,
    colors: {
      ...mainScheme.colors,
      text: '#4A4A4A',
      grayText: '#9B9B9B',
      border: '#ededed',
      button: '#EFF0F5',
      buttonHover: '#DEDEFF',
      closeButton: '#9b9b9b',
      background: '#F8F9FD',
      logo: '#000',
      loginModal: '#F8F9FD',
      input: '#F8F9FD',
      inputText: '#4a4a4a',
      inputBorder: '#4a4a4a',
      radio: '#EFF0F5',
      radioActive: '#DEDEFF',
      radioTextActive: '#7070FF',
      radioText: '#9B9B9B',
    },
  };
};
