import React, { useEffect, useRef } from 'react';

const CSPFlagIcon = ({ src, title, size, style = {}, code }) => {
  const spanRef = useRef(null);
  useEffect(() => {
    // eslint-disable-next-line no-param-reassign
    style = {
      ...style,
      display: 'flex',
    };
    spanRef.current.innerHTML = '';
    const img = window.__lskjs.GM_addElement(spanRef.current, 'img', {
      src: `https://cdnjs.cloudflare.com/ajax/libs/flag-icon-css/2.8.0/flags/4x3/${code}.svg`,
    });
    Object.keys(style).forEach((key) => {
      if (typeof style[key] === 'number') {
        img.style[key] = `${String(style[key])}px`;
      } else {
        img.style[key] = style[key];
      }
    });
  }, [code]);
  return <span ref={spanRef}></span>;
};

export default CSPFlagIcon;
