import BaseBGAvatar from '@buzzguru/ui/Avatar';
import styled from '@emotion/styled';
import getTheme from '@lskjs/theme/getTheme';
import React from 'react';

import BaseCSPAvatar from './CSPAvatar';

const BGAvatar = styled(BaseBGAvatar)`
  && > * img {
    background-color: ${(props) => getTheme(props.theme, 'colors.background')};
  }
`;
const CSPAvatar = styled(BaseCSPAvatar)`
  && > * img {
    background-color: ${(props) => getTheme(props.theme, 'colors.background')};
  }
`;

const Avatar = (props) => {
  if (window.csp) {
    return <CSPAvatar {...props} />;
  }
  return <BGAvatar {...props} />;
};

export default Avatar;
