/* global document window */

// @match        *://igram.io/*

import { addGlobalStyle } from '../utils';

export async function loaderto() {
  const location = document?.location?.href || '';
  if (!location.includes('loader.to')) return { status: 'ignoreDomain' };

  const spam = 'nav, .center > *,  footer, iframe, small a';

  addGlobalStyle(`
${spam} {
  display: none !important;
}
.center > .input-group, .center > .input-group + div , .center > #ds{
  display: block !important;
}
  `);

  return { status: 'success' };
}

export default loaderto;
