/* eslint-disable no-undef */
export * from '@lskjs/env';

export const config = window?.__lskjs?.config || (typeof CONFIG !== 'undefined' && CONFIG) || window?.CONFIG || {};
export const appConfig = config?.appConfig || {};
export const baseURL = appConfig?.baseURL || '/';
export const stage = window?.__lskjs?.stage || (typeof STAGE !== 'undefined' && STAGE) || window?.STAGE || 'prod';
export const isProd = stage === 'prod' || stage === 'production';
export const isDebug = !isProd;
export const version =
  window?.__lskjs?.version || (typeof VERSION !== 'undefined' && VERSION) || window?.VERSION || '???';
export const plugins = window?.__lskjs?.plugins ||
  (typeof PLUGINS !== 'undefined' && PLUGINS) ||
  window?.PLUGINS || ['*']; // TODO: для обратной совместимости, когда нибудь удалить
export const { isExtension } = window;
export const isUserscript = !window?.isExtension; // TODO: вообще не корректно, но надо подумать как грамотнее это делать

export const build = isUserscript ? `US-${version}-${stage}` : `EXT-${version}-${stage}`;

