/* global document */
import Tooltip from '@buzzguru/ui/Tooltip';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import getRequestParams from '../../../utils/getRequestParams';
import getStats from '../../../utils/getStats';
import { Typography, Wrapper } from './Stats.styles';

const Stats = ({ entity, i18, view, ...props }) => {
  const { item } = entity;
  const [reqParams, setReqParams] = useState({ type: 'youtube' });
  const { type } = reqParams;
  if (type === 'youtubeVideo') {
    if ((item?.likes || 0) < (reqParams?.likes || 0)) item.likes = reqParams?.likes;
  }
  const stats = getStats({ item, type, i18 });
  useEffect(() => {
    getRequestParams().then((res) => setReqParams(res));
  }, [document?.location?.href || '']);
  return (
    <Wrapper id="stats" view={view} {...props}>
      {stats.map((stat) => (
        <Tooltip place="top" key={stat.id} id={stat.id} overlay={stat.title}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              paddingRight: 16,
              flexDirection: 'row',
            }}
          >
            {stat.icon}
            <Typography view="extra-1" style={{ paddingLeft: 8 }} color="#ffffff">
              {stat.value}
            </Typography>
          </div>
        </Tooltip>
      ))}
    </Wrapper>
  );
};

Stats.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  view: PropTypes.oneOf(['short', 'long']),
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Stats);
