import React, { useEffect, useRef } from 'react';

const CSPAvatar = ({ src, title, size, cspStyle: style = {}, defaultAvatar }) => {
  const spanRef = useRef(null);
  useEffect(() => {
    // eslint-disable-next-line no-param-reassign
    style = {
      ...style,
      maxWidth: 'none',
    };
    spanRef.current.innerHTML = '';
    const img = window.__lskjs.GM_addElement(spanRef.current, 'img', {
      src: src || defaultAvatar,
      alt: title,
      title,
      width: size,
      height: size,
    });
    Object.keys(style).forEach((key) => {
      if (typeof style[key] === 'number') {
        img.style[key] = `${String(style[key])}px`;
      } else {
        img.style[key] = style[key];
      }
    });
  }, [src || defaultAvatar]);
  return <span ref={spanRef}></span>;
};

export default CSPAvatar;
