/* global document */
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import getRequestParams from '../../utils/getRequestParams';
import Alert from './Alert';
import { Wrapper } from './BottomContent.styles';
import Mention from './Mention';
import Stats from './Stats';

const BottomContent = ({ entity, app }) => {
  const { item } = entity;
  const [parsedItem, setParsedItem] = useState({});
  const subscribers = parsedItem?.subscribers || 0;
  const isAlert = !item?._id;
  useEffect(() => {
    getRequestParams().then((res) => setParsedItem(res || {}));
  }, [document?.location?.href || '']);
  return (
    <Wrapper isAlert={isAlert}>
      {isAlert ? (
        <Alert subscribers={subscribers} />
      ) : (
        <>
          <Stats entity={entity} />
          <Mention entity={entity} />
        </>
      )}
    </Wrapper>
  );
};

BottomContent.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  app: PropTypes.objectOf(Object).isRequired,
};

export default BottomContent;
