/* eslint-disable no-param-reassign */
/* global document window */

// @match        *://modash.io/*
// @match        *://*.modash.io/*

import { addXMLRequestCallback } from '../utils';

const isDev = 0;
const host = isDev ? 'http://127.0.0.1:8000' : 'https://worker.buzz.guru';

function getType(url) {
  const webOverviewRegexp = /^.*\/api\/discovery\/show-profile.*$/;
  const webFullRegexp = /^.*\/api\/discovery\/report.*$/;
  const isWebOverview = webOverviewRegexp.test(url);
  const isWebFull = webFullRegexp.test(url);

  let type = '';
  if (isWebOverview) type = 'web_overview';
  if (isWebFull) type = 'web_full';
  return type;
}

function getRoute(profile) {
  const { profileType } = profile;

  let platform = '/';
  if (profileType === 'YOUTUBE') platform = 'youtube';
  if (profileType === 'INSTAGRAM') platform = 'instagram';
  if (profileType === 'TIKTOK') platform = 'tiktok';

  // return `modash/channel_events/${platform}/`;
  return `${platform}_channel_modash_events/`;
}

export async function modash() {
  const location = document?.location?.href || '';
  if (!location.includes('marketer.modash.io')) return { status: 'ignoreDomain' };

  addXMLRequestCallback(async (xhr) => {
    const responseHeaders = {};
    const responseHeadersArray = xhr
      .getAllResponseHeaders()
      .split('\n')
      .filter((str) => str);
    responseHeadersArray.forEach((header) => {
      const [key, value] = header.split(/: (.+)/);
      responseHeaders[key] = value;
    });
    const payload = {
      url: xhr.meta.url,
      request: {
        method: xhr.meta.method,
        headers: xhr.requestHeaders,
        body: xhr.meta.body,
      },
      response: {
        headers: responseHeaders,
        text: xhr.responseText,
      },
    };

    const type = getType(payload.url);
    if (!type) return;

    const { error, profile } = JSON.parse(payload.response.text) || {};
    if (error || !profile) return;

    const route = getRoute(profile);
    if (!route) return;

    console.log('[R]', payload); // eslint-disable-line no-console

    window.__lskjs.GM_xmlhttpRequest({
      method: 'POST',
      url: `${host}/api/${route}`,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      data: JSON.stringify({ type, data: profile.profileData }),
    });
  });
  return { status: 'success' };
}

export default modash;
