import BuzzGuruIcon from '@buzzguru/ui/assets/icons/buzzguru-logo-custom';
import BaseQuality from '@buzzguru/ui/kit/Quality';
import styled from '@emotion/styled';
import getTheme from '@lskjs/theme/getTheme';
import Dropdown from '@lskjs/ui/Dropdown';
import { injectGlobal } from 'emotion';

export const BuzzGuru = styled(BuzzGuruIcon)`
  margin-right: 22px;
  width: 144px;
  color: ${(p) => getTheme(p.theme, 'colors.logo')};
`;

export const Wrapper = styled('div')`
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  white-space: nowrap;
  position: relative;
`;

export const Quality = styled(BaseQuality)`
  height: 36px;
  width: 36px;
  flex-direction: row;
  > div {
    &:first-child {
      width: 36px;
      height: 36px;
      min-height: 36px;
      min-width: 36px;
      font-size: 18px;
      line-height: 30px;
      font-weight: normal;
    }
    &:last-child {
      display: none;
    }
  }
  @media screen and (max-width: 1460px) {
    display: ${(props) => (props.extView === 'long' ? 'none' : 'flex')};
  }
`;

export const DropdownItem = styled(Dropdown.Item)`
  cursor: pointer;
  &:not(:last-child) {
    border-bottom: 1px solid ${(p) => getTheme(p.theme, 'colors.border')};
  }
`;

export const FlagWrapper = styled('div')`
  background: ${(p) => getTheme(p.theme, 'colors.button')};
  border-radius: 4px;
  padding: 6px 4px;
  margin-right: 8px;
  @media screen and (max-width: 1460px) {
    display: ${(props) => (props.extView === 'long' ? 'none' : 'flex')};
  }
`;

export const LanguageWrapper = styled('div')`
  background: ${(p) => getTheme(p.theme, 'colors.button')};
  color: ${(p) => getTheme(p.theme, 'colors.text')};
  border-radius: 4px;
  padding: 2px 6px;
  margin-right: 8px;
  line-height: 20px;
  @media screen and (max-width: 1460px) {
    display: ${(props) => (props.extView === 'long' ? 'none' : 'flex')};
  }
`;
export const Title = styled('div')`
  background: transparent;
  color: ${(p) => getTheme(p.theme, 'colors.text')};
  font-size: 18px;
  margin-right: 12px;
  white-space: nowrap;
`;

export const globalStyles = () => injectGlobal`
  .tooltip {
    z-index: 99999;
  }
`;

export default Wrapper;
