import SettingsIcon from '@buzzguru/ui/assets/icons/settings';
import TypographyBase from '@buzzguru/ui/Typography2';
import { css } from '@emotion/core';
import styled from '@emotion/styled';
import Button from '@lskjs/button';
import Modal from '@lskjs/modal';
import getTheme from '@lskjs/theme/getTheme';

export const Wrapper = styled('div')`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: row;
  /* width: 50%; */
  /* height: 36px; */
`;

export default Wrapper;

export const IconWrapper = styled('div')`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 19px;
  margin-right: 12px;
  background-color: ${(p) => getTheme(p.theme, 'colors.button')};
`;

export const ModalItem = styled('div')`
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  > div {
    width: auto;
  }
  margin-bottom: 20px;
`;

export const StyledButton = styled(Button)`
  background: ${(p) => (p.active ? getTheme(p.theme, 'colors.radioActive') : getTheme(p.theme, 'colors.radio'))};
  color: ${(p) => (p.active ? getTheme(p.theme, 'colors.radioTextActive') : getTheme(p.theme, 'colors.radioText'))};
  ${(p) =>
    !p.active &&
    css`
      &:focus {
        background: getTheme(p.theme, 'colors.radio');
        color: getTheme(p.theme, 'colors.radioText');
      }
      &:focus &:hover {
        background: getTheme(p.theme, 'colors.radioActive');
        color: getTheme(p.theme, 'colors.text');
      }
    `}
`;

export const MainButton = styled(Button)`
  background: ${(p) => getTheme(p.theme, 'colors.main')} !important;
  color: #fff !important;
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.mainHover')} !important;
  }
  &:active,
  &:focus {
    background-color: ${(p) => getTheme(p.theme, 'colors.main')};
  }
`;

export const ListButton = styled(Button)`
  border: 1px solid ${(p) => getTheme(p.theme, 'colors.main')};
  color: ${(p) => getTheme(p.theme, 'colors.main')};
  &:hover,
  &:active,
  &:focus {
    border: 1px solid ${(p) => getTheme(p.theme, 'colors.main')};
    color: ${(p) => getTheme(p.theme, 'colors.main')};
    background-color: rgb(240, 240, 255);
  }
`;

export const ModalContent = styled(Modal.Content)`
  background-color: ${(p) => getTheme(p.theme, 'colors.background')};
  color: ${(p) => getTheme(p.theme, 'colors.text')};
`;

export const Typography = styled(TypographyBase)`
  color: ${(p) => getTheme(p.theme, 'colors.text')};
`;

export const CloseButton = styled(Button)`
  width: 36px;
  height: 36px;
  margin-left: 4px;
  color: #9b9b9b;
  transition: transform 0.2s;
  &:hover {
    color: ${(p) => getTheme(p.theme, 'colors.text')} !important;
    transform: scale(1.3);
  }
`;

export const HoverButton = styled(Button)`
  background-color: ${(p) => getTheme(p.theme, 'colors.button')};
  color: ${(p) => getTheme(p.theme, 'colors.text')};
  && * {
    color: ${(p) => getTheme(p.theme, 'colors.text')};
  }
  width: 36px;
  height: 36px;
  border-radius: 8px;
  &:hover {
    background-color: ${(p) => getTheme(p.theme, 'colors.buttonHover')} !important;
  }
  &:active,
  &:focus {
    color: ${(p) => getTheme(p.theme, 'colors.text')};
  }
  @media screen and (max-width: 1550px) {
    display: ${(props) => (props.extView === 'long' ? 'none' : 'flex')};
  }
`;

export const Settings = styled(SettingsIcon)`
  color: ${(p) => getTheme(p.theme, 'colors.text')};
`;

export const ButtonsWrapper = styled('div')`
  display: flex;
  justify-content: flex-end;
  width: 100%;
  flex-direction: row;
  > button {
    ${(p) => p.block && 'flex-grow: 1;'}
    &:first-of-type {
      border-radius: 8px 0 0 8px;
    }
    &:last-of-type {
      border-radius: 0 8px 8px 0;
    }
  }
`;

export const ButtonsThemeWrapper = styled('div')`
  display: flex;
  justify-content: flex-end;
  width: 100%;
  flex-direction: row;
  margin-bottom: 35px;
  > button {
    font-size: 13px;
    width: 100%;
    ${(p) => p.block && 'flex-grow: 1;'}
    &:first-of-type {
      border-radius: 8px 0 0 8px;
    }
    &:last-of-type {
      border-radius: 0 8px 8px 0;
    }
  }
`;

export const TitleWrapper = styled('div')`
  margin: 0 -16px 10px -16px;
  border-bottom: 1px solid ${(p) => getTheme(p.theme, 'colors.border')};
  button {
    color: ${(p) => getTheme(p.theme, 'colors.text')} !important;
  }
`;

export const UserWrapper = styled('div')`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
`;
