import TypographyBase from '@buzzguru/ui/Typography2';
import styled from '@emotion/styled';
import Link from '@lskjs/link';
import getTheme from '@lskjs/theme/getTheme';

export const Wrapper = styled('div')`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: row;
  > a {
    display: flex;
    text-decoration: none;
    &:not(:last-of-type) {
      margin-right: 16px;
    }
  }
  @media screen and (max-width: 1380px) {
    display: ${(props) => (props.view === 'long' ? 'none' : 'flex')};
  }
`;

export const MoreWrapper = styled('div')`
  width: 36px;
  height: 36px;
  border-radius: 8px;
  background-color: ${(props) => getTheme(props.theme, 'colors.button')};
  font-size: 13px;
  z-index: 1;
  color: ${(props) => getTheme(props.theme, 'colors.text')};
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
`;

export const Typography = styled(TypographyBase)`
  color: ${(props) => getTheme(props.theme, 'colors.text')};
`;

export const TiktokLink = styled(Link)`
  margin-right: -12px;
  border-radius: 50%;
  && * img {
    border: 2px solid ${(props) => getTheme(props.theme, 'colors.background')};
  }
`;

