import createForm from '@lskjs/form/createForm';
import Input from '@lskjs/form-control-input';
import Password from '@lskjs/form-control-password';
import Link from '@lskjs/link';
import Modal from '@lskjs/modal';
import T from '@lskjs/t';
import PropTypes from 'prop-types';
import React from 'react';

import AuthLoginFormView from './AuthLoginFormView';
import { Button, BuzzGuru, globalStyles, ModalContent, Typography } from './Login.styles';

globalStyles();

const AuthLoginForm = createForm({
  withI18: true,
  view: AuthLoginFormView,
  controls: {
    email: {
      component: Input,
      title: 'authForm.email',
      placeholder: 'authForm.email',
      props: { autocomplete: 'username' },
      validator: {
        presence: {
          allowEmpty: false,
          message: <T name="errors.auth.fieldShouldntBeEmpty" />,
        },
        email: {
          message: <T name="errors.auth.emailIncorrect" />,
        },
      },
      view: 'email',
      initialValue: '',
      _required: true,
    },
    password: {
      component: Password,
      title: 'authForm.password',
      placeholder: 'authForm.password',
      props: { autocomplete: 'current-password' },
      validator: {
        presence: {
          allowEmpty: false,
          message: <T name="errors.auth.fieldShouldntBeEmpty" />,
        },
      },
      initialValue: '',
      _required: true,
    },
  },
});

const Login = ({ type, login }) => {
  const handleLogin = async (values) => {
    const email = (values?.email || '').toLowerCase();
    const session = await login({ ...values, email });
    console.log({ session });
    window?.__lskjs?.GM_setValue('session', session);
    console.log('session', window?.__lskjs?.GM_getValue('session'));
    app.redirect('');
  };
  return (
    <>
      <Modal
        size="small"
        trigger={
          <Button paint="primary" style={{ marginRight: 24, padding: '11px 24px' }}>
            <T name="buttons.login" />
          </Button>
        }
      >
        <ModalContent>
          <BuzzGuru />
          <AuthLoginForm onSubmit={handleLogin} type={type} />
        </ModalContent>
      </Modal>
      <Typography view="title-1" style={{ paddingRight: 4, whiteSpace: 'nowrap' }}>
        <T name="signInView.newUser" />
      </Typography>
      <Typography view="title-1">
        <Link
          href="https://buzzguru.com/auth/signup"
          target="_blank"
          style={{ color: '#7070ff', textDecoration: 'none', whiteSpace: 'nowrap' }}
        >
          <T name="signInView.signUp" />
        </Link>
      </Typography>
    </>
  );
};

Login.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  login: PropTypes.func.isRequired,
};

export default Login;
