/* eslint-disable prettier/prettier */
import Link from '@lskjs/link';
import T from '@lskjs/t';
import { FastField } from 'formik';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';

import { CenterWrapper, Error, FormSubmit, StyledForm, Typography, TypographyGray } from './Login.styles';

const AuthLoginFormView = ({ control, status, errors, i18, type }) => (
  <>
    <Typography view="huge-10" paragraph>
      <T name="signInView.entrance" />
    </Typography>
    <StyledForm className="ant-form ant-form-vertical">
      <FastField {...control('email')} placeholder={i18.t('authForm.email')} />
      <FastField
        {...control('password')}
        placeholder={i18.t('authForm.password')}
        info={(
          <Link
            href="https://buzzguru.com/auth/restore"
            target="_blank"
            tabIndex="-1"
            style={{ color: '#7070ff', textDecoration: 'none', margin: type === 'instagram' ? '-20px 0 0 auto' : '' }}
          >
            <T name="signInView.forgotPassword" />
          </Link>
        )}
      />
      {errors?.onSubmit ? (
        <Error>
          <T name="extension.errors.login" />
        </Error>
      ) : ''}
      <FormSubmit block status={status} errors={errors} style={{ padding: 18, margin: '48px auto 0', width: '90%' }}>
        <T name="buttons.login" />
      </FormSubmit>
    </StyledForm>
    <CenterWrapper>
      <TypographyGray view="title-5" style={{ paddingRight: 4, }}>
        <T name="signInView.newUser" />
      </TypographyGray>
      <Typography view="title-6">
        <Link
          href="https://buzzguru.com/auth/signup"
          target="_blank"
          style={{ color: '#7070ff', textDecoration: 'none' }}
        >
          <T name="signInView.signUp" />
        </Link>
      </Typography>
    </CenterWrapper>
    <TypographyGray view="title-2" style={{ marginLeft: 'auto', marginTop: 96 }}>
      {`Copyright © ${new Date().getFullYear()}`}
    </TypographyGray>
  </>
)
AuthLoginFormView.propTypes = {
  control: PropTypes.func.isRequired,
  status: PropTypes.string,
  errors: PropTypes.objectOf(Object),
  i18: PropTypes.objectOf(Object).isRequired,
  type: PropTypes.string,
};

AuthLoginFormView.defaultProps = {
  status: undefined,
  errors: undefined,
  type: '',
};

export default inject('i18')(AuthLoginFormView);
