const getSubscribers = (str) => {
  let value = str.replace(/,/g, '.');
  const matches = value.match(/[0-9]*\.?[0-9]*/g);
  if (matches && matches[0]) {
    value = Number(matches[0]);
  }
  const postfix = str.replace(/[0-9]/g, '');

  const ratioMap = {
    тыс: 1000,
    млн: 1000000,
    K: 1000,
    M: 1000000,
    B: 1000000000,
  };

  let ratio = null;
  if (postfix) {
    Object.keys(ratioMap).forEach((key) => {
      if (!ratio && postfix.includes(key)) {
        // console.log(ratioMap[key], 111);
        ratio = ratioMap[key];
      }
    });
  }
  if (ratio) {
    value *= ratio;
  }
  return value;
};

const getVideoPrimaryInfoRenderer = (initialData) => {
  if (!initialData) throw new Err('Missed required argument: initialPlayerResponse');

  const contents = get(initialData, 'contents.twoColumnWatchNextResults.results.results.contents', []);

  if (!Array.isArray(contents)) throw new Err('invalid type: contents is not array');

  const videoPrimaryInfoRenderer = contents.filter((item) => get(item, 'videoPrimaryInfoRenderer'));

  return get(videoPrimaryInfoRenderer, '0.videoPrimaryInfoRenderer', {});
};

const normalizeValue = (value, type) => {
  if (typeof value !== 'string') return value;

  const matches = value.match(/\d/g);

  if (matches === null) return 0;
  let res = Number(matches.join(''));
  if (type === 'comments' && value[value.length - 1] === 'K') res *= 1000;
  return res;
};

const getLikes = (videoPrimaryInfoRenderer) => {
  if (!videoPrimaryInfoRenderer) throw new Err('Missed required argument: videoPrimaryInfoRenderer');

  const likePath = get(videoPrimaryInfoRenderer, 'videoActions.menuRenderer.topLevelButtons[0].toggleButtonRenderer.defaultText'); //eslint-disable-line
  if (!likePath) {
    return null;
  } else if (likePath.hasOwnProperty('accessibility')) { //eslint-disable-line
    return normalizeValue(get(likePath, 'accessibility.accessibilityData.label'));
  } else if (Object.hasOwnProperty.call(likePath, 'simpleText')) {
    return normalizeValue(parseInt(likePath.simpleText, 10));
  }
  return 0;
};

export default async () => {
  let pathName = document?.location?.href || '';
  const youtubeRegexp = /(https?:\/\/)?(www\.)?(youtube\.com|youtu\.be)\/[\/\wа-яА-Яё\-_\?=&]+/gim;
  const instagramRegexp = /(?:(?:http|https):\/\/)?(?:www.)?(?:instagram.com|instagr.am)\/([A-Za-z0-9-_.]+)/gim;
  const tiktokRegexp = /tiktok.com\/@([a-zA-Z0-9_.]+)/gim;
  const twitchRegexp = /^(?:http(?:s?):\/\/)?(?:w{0,3})\.?twitch\.tv\/([a-zA-Z0-9_]+)/gim;
  const twitchVideo = /twitch\.tv\/videos\/(\d+)/i;
  if (pathName.match(youtubeRegexp)) {
    if (pathName.includes('/watch?')) {
      const { v } = qs.parse(document.location.search.slice(1));
      if (!v) return null;
      const url = `https://www.youtube.com/watch?v=${v}&app=desktop&bpctr=9999999999`;
      const videoResponse = await window.fetch(url).then((res) => res.text());
      const [, json2] = videoResponse.match(/(?<=var ytInitialData\s=\s)(.*?)(?=;<\/script>)/) || [];
      const ytInitialResponse = JSON.parse(json2);
      const videoPrimaryInfoRenderer = getVideoPrimaryInfoRenderer(ytInitialResponse);
      const likes = getLikes(videoPrimaryInfoRenderer);
      // console.log({ likes });
      return { type: 'youtubeVideo', id: v, likes, subscribers: 'noData' };
    }
    const isChannelHTML = !!document.querySelector('ytd-browse[role="main"] #channel-header');
    if (isChannelHTML) {
      const f = document.querySelector('ytd-browse[role="main"] form#form');
      if (f && f.action) {
        pathName = new URL(f.action).pathname;
      }
    }
    let url;
    if (pathName.includes('/search')) {
      url = pathName.replace(/\/search$/, '/about');
    } else {
      const pathAndId = /\/(c|channel|user)\/([^\/?#\s()]+)/.exec(decodeURIComponent(pathName));
      if (!pathAndId) return null;
      url = `/${pathAndId[1]}/${pathAndId[2]}/about`;
    }
    const channelResponse = await window.fetch(url).then((res) => res.text());
    const [, json2] = channelResponse.match(/(?<=var ytInitialData\s=\s)(.*?)(?=;<\/script>)/) || [];
    const ytInitialResponse = JSON.parse(json2);
    const metadata = ytInitialResponse.metadata.channelMetadataRenderer;
    const _subscribers =
      get(ytInitialResponse, 'header.c4TabbedHeaderRenderer.subscriberCountText.runs[0].text', null) ||
      get(ytInitialResponse, 'header.c4TabbedHeaderRenderer.subscriberCountText.simpleText', null); // eslint-disable-line max-len
    let subscribers = null;
    if (typeof _subscribers === 'string') {
      subscribers = getSubscribers(_subscribers);
    }
    const providerId = metadata.externalId;
    const avatars = get(ytInitialResponse, 'header.c4TabbedHeaderRenderer.avatar.thumbnails', []);
    const avatar = (avatars.sort((a, b) => (a?.width || 0) > (b?.width || 0)) || [])[0]?.url || '';
    const name = metadata.title;
    return {
      type: 'youtube',
      id: providerId,
      idType: 'providerId',
      avatar,
      name,
      subscribers,
      url: document?.location?.href || '',
    };
  }
  if (pathName.match(instagramRegexp)) {
    if (pathName.includes('/p/')) return null;
    if (pathName.includes('/stories/')) return null;
    if (pathName.includes('/tv/')) return null;
    const match = instagramRegexp.exec(pathName);
    if (match) {
      // const channelResponse = await fetchInstagram(`https://www.instagram.com/${match[1]}?__a=1`);
      // const channelResponse = await window.fetch(`https://www.instagram.com/${match[1]}?__a=1`).then((res) => {
      //   const data = JSON.parse(res.text());
      //   return data?.graphql?.user || {};
      // });
      // console.log({ dddd: channelResponse });
      // const username = channelResponse?.username;
      // const followers = channelResponse?.edge_followed_by?.count;
      // const avatar = channelResponse?.profile_pic_url;
      return {
        type: 'instagram',
        id: match[1],
        // subscribers: followers,
        subscribers: 'noData',
        name: match[1],
        avatar: null,
        url: document?.location?.href || '',
      };
    }
  }
  if (pathName.match(tiktokRegexp)) {
    if (pathName.includes('/video/')) return null;
    const match = tiktokRegexp.exec(pathName);
    if (match) {
      return { type: 'tiktok', id: match[1], subscribers: 'noData' };
    }
  }
  if (pathName.match(twitchRegexp)) {
    if (pathName.includes('/videos/')) {
      const match = twitchVideo.exec(pathName);
      const id = match?.[1];
      if (!id) return null;
      return { type: 'twitchStream', id, subscribers: 'noData' };
    }
    const match = twitchRegexp.exec(pathName);
    return { type: 'twitch', id: match[1].toLowerCase(), subscribers: 'noData' };
  }
  return null;
};
